.table{
    // margin-top: 20px;
    overflow: auto;

    .tableHead{
        background-color: #F9FAFB;

        .tableCell{
            font-weight: 700;
            color: gray;
            text-transform: uppercase;
            font-size: 16px;

            .Member{
                color: Blue;
            }
        }
    }

    .Member{
        color: #000080;
    }
    .Worker{
        color: crimson;
    }
    .Minister{
        color: green;
    }
    .cellWrapper{
        display: flex;
        align-items: center;
        color: gray;
        font-size: 15px;

        .image{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            margin-right: 10px;
            object-fit: cover;
        }
        .phone{
            color: rgb(181, 181, 181);
            display: block;
        }
    }

    .status{
        width: max-content;
        padding: 5px;
        font-weight: 700;
        border-radius: 5px;

        &.Member{
            color: #000080;
            background-color: #0000808e;
        }

        &.Absent{
            color: goldenrod;
            background-color: rgba(218, 165, 32, 0.174);
        }
    }

    .action{

        span{
            margin-right: 13px;
            // padding: 5px;
            font-size: 12px;
            cursor: pointer;
            padding: 2px 5px;
            border-radius: 5px;
            color: darkblue;
            border: 1px dotted rgba(0, 0, 139, 0.187);

            &.delete{
                color: crimson;
                border: 1px dotted rgba(220, 20, 60, 0.187);
            }
            &.edit{
                color: green;
                border: 1px dotted rgba(0, 128, 0, 0.187);
            }
            &.view{
                color: darkblue;
                border: 1px dotted rgba(0, 0, 139, 0.187);
            }
        }
    }

}
